import { RouterModule, Routes } from '@angular/router';
import { AllocationComponent } from './allocation.component';
import { ModuleWithProviders } from '@angular/core';

const allocationRoutes: Routes = [
    {
        path: '', component: AllocationComponent
    }
];

export const allocationRouting: ModuleWithProviders = RouterModule.forChild(allocationRoutes);



