import { Component, OnInit } from '@angular/core';
import { TravelService } from '../services/travel.service';
import { FormService } from '../services/form.service';
import { ActivatedRoute } from '@angular/router';
import { VehicleService } from '../services/vehicle.service';
import { DriverService } from '../services/driver.service';

@Component({
  selector: 'app-allocation',
  templateUrl: './allocation.component.html',
  styleUrls: ['./allocation.component.scss']
})
export class AllocationComponent implements OnInit {

  protected model: any = {};
  protected isLinear = false;
  protected checked = false;
  protected terminal = false;
  protected id;

  protected vehicles = [];
  protected vehiclesHeader = ['Veículo', 'Placa', 'Marca', 'Capacidade'];

  protected drivers = [];
  protected driversHeader = ['Nome', 'Cpf', 'Cnh', 'Registro'];

  protected selectedDriver: number;
  protected selectedVehicle: number;


  constructor(private travelService: TravelService,
              private route: ActivatedRoute,
              private formService: FormService,
              private vehicleService: VehicleService,
              private driverService: DriverService) {
                 this.id = this.route.snapshot.params['id'];
               }

  ngOnInit() {
     this.formService.getById(this.id).subscribe((data) => {
        this.model = data;
      }, error => {
        console.error(error);
      }
    );

    this.vehicleService.vehiclesObs.subscribe((data) => {
      for (const vehicleData of data) {
          this.vehicles.push(vehicleData);
      }
    });

    this.driverService.driversObs.subscribe((data) => {
      for (const driverData of data) {
          this.drivers.push(driverData);
      }
    });
  }

  selectDriver(id: number) {
    this.selectedDriver = id;
  }

  selectVehicle(id: number) {
    this.selectedVehicle = id;
  }

  confirmTravel() {
    const form = {
      formId: this.id,
      vehicle: this.selectedVehicle
    };

    this.travelService.create(form).subscribe(data => {
      console.log('ok');
    });
  }
}



